import * as React from 'react';

declare global {
  interface Window {
    RichTextEditor: any;
  }
}

type T_RTE = {
  contentHtml?: string | null;
  setContentHtml?: any;
};

export default function RichTextEditor({ contentHtml, setContentHtml }: T_RTE) {
  const refdiv = React.useRef(null);
  const [rte, setRte] = React.useState<any>(null);

  React.useEffect(() => {
    if (refdiv.current && window.RichTextEditor) {
      const editor = new window.RichTextEditor(refdiv.current);
      editor.setHTMLCode(`<div style="font-family: 'Inter', sans-serif;">${contentHtml}</div>`);
      setRte(editor);

      // Adding onChange event listener
      editor.attachEvent('change', function () {
        // setContentHtml((prev) => ({ ...prev, contentHtml: editor.getHTMLCode() }));
        if (setContentHtml) {
          setContentHtml(editor.getHTMLCode());
        }
      });
    }

    // Cleanup function to remove event listener when component unmounts
    return () => {
      if (rte) {
        rte.detachEvent('change');
      }
    };
  }, []);

  return <div ref={refdiv} />;
}
